"use client";

import { useState } from "react";
import { Etapa0 } from "../components/Etapa0";
import { Etapa1 } from "../components/Etapa1";
import { Etapa2 } from "../components/Etapa2";

// 1. INTERFACE DE DADOS COMPLETAMENTE ATUALIZADA
interface FormData {
  nome: string;
  dataNascimento: string;
  sexo: string;
  whatsapp: string; // Padronizado para 'whatsapp'
  email: string;
  estado: string;
  cidade: string;
  voluntario: boolean | undefined;
  tipoVoluntario: 'existente' | 'interessado' | null; // Tipo mais específico
  camiseta: string;
  membroDesde: string;
  voluntarioDesde: string;
  ministerio: string[];
  batizado: boolean;
  batizadoDesde: string;
}

export default function FormularioPage() {
  const [etapa, setEtapa] = useState(0);

  // 2. ESTADO INICIAL ATUALIZADO com todos os campos
  const [form, setForm] = useState<FormData>({
    nome: "",
    dataNascimento: "",
    sexo: "",
    whatsapp: "",
    email: "",
    estado: "",
    cidade: "",
    voluntario: undefined,
    tipoVoluntario: null,
    camiseta: "",
    membroDesde: "",
    voluntarioDesde: "",
    ministerio: [],
    batizado: false,
    batizadoDesde: "",
  });

  // 3. 'handleChange' ATUALIZADO com lógica especial para a Etapa 0
  const handleChange = (e: React.ChangeEvent<any>) => {
    const { name, value, type, checked } = e.target;

    // Lógica especial para a seleção da Etapa 0
    if (name === "tipoVoluntario") {
      setForm((prev) => ({
        ...prev,
        voluntario: true, // Garante que o usuário irá para o fluxo de voluntário
        tipoVoluntario: value, // Define qual opção foi selecionada
      }));
      return; // Encerra a função aqui para este caso
    }

    // Lógica genérica para os outros campos
    let finalValue = value;
    if (name === "batizado") {
        finalValue = (value === "true" || value === true);
    } else if (type === "checkbox") {
        finalValue = name === "ministerio" ? value : checked;
    }

    setForm((prev) => ({ ...prev, [name]: finalValue }));
  };

  // Suas funções de navegação estão corretas e foram mantidas
  const handleNext = () => {
    setEtapa((currentEtapa) => {
      if (currentEtapa === 0) {
        if (form.tipoVoluntario === 'existente') return 2;
        if (form.tipoVoluntario === 'interessado') return 1;
      }
      if (currentEtapa === 1) {
        return 2;
      }
      return currentEtapa;
    });
  };

  const handleBack = () => {
    setEtapa((currentEtapa) => {
      if (currentEtapa === 1) {
        return 0;
      }
      if (currentEtapa === 2) {
        if (form.tipoVoluntario === 'existente') return 0;
        if (form.tipoVoluntario === 'interessado') return 1;
      }
      return currentEtapa;
    });
  };

  const handleSubmit = async () => {
    console.log("Formulário enviado:", form);
    alert("Inscrição enviada com sucesso!");
    // Aqui você pode adicionar a lógica de envio para sua API
  };

  // A renderização dos componentes continua a mesma
  return (
    <div className="container mx-auto p-4 sm:p-8 max-w-2xl">
      {etapa === 0 && (
        <Etapa0
          form={form}
          handleChange={handleChange}
          onNext={handleNext}
        />
      )}
      {etapa === 1 && (
        <Etapa1
          form={form}
          handleChange={handleChange}
          onNext={handleNext}
          onBack={handleBack}
        />
      )}
      {etapa === 2 && (
        <Etapa2
          form={form}
          handleChange={handleChange}
          onBack={handleBack}
          onSubmit={handleSubmit}
        />
      )}
    </div>
  );
}