// components/Etapa2.tsx
/* eslint-disable react/jsx-key */
"use client";

import { motion } from "framer-motion";
import { useState } from "react";

// Suas constantes de MESES e ANOS continuam aqui...
const MESES = [
    { value: "01", label: "Janeiro" }, { value: "02", label: "Fevereiro" },
    { value: "03", label: "Março" }, { value: "04", label: "Abril" },
    { value: "05", label: "Maio" }, { value: "06", label: "Junho" },
    { value: "07", label: "Julho" }, { value: "08", label: "Agosto" },
    { value: "09", label: "Setembro" }, { value: "10", label: "Outubro" },
    { value: "11", label: "Novembro" }, { value: "12", label: "Dezembro" },
];
const currentYear = new Date().getFullYear();
const ANOS = Array.from({ length: currentYear - 2010 + 1 }, (_, i) => String(2010 + i));


// A interface agora precisa saber sobre 'tipoVoluntario'
interface FormData {
  nome: string;
  tipoVoluntario: 'existente' | 'interessado' | null;
  // ... outros campos
  camiseta: string;
  membroDesde: string;
  voluntarioDesde: string;
  ministerio: string[];
  batizado: boolean;
  batizadoDesde: string;
}

interface Etapa2Props {
  form: FormData;
  handleChange: (e: React.ChangeEvent<any>) => void;
  onBack: () => void;
  onSubmit: () => Promise<void> | void;
}

const ministeriosDisponiveis = [
  "Worship", "Integração", "V.I.P", "Kids", "Creative", "Parking", "Teens", "RdkBrave",
  "Lounge", "Connect", "Produção", "Cerimonial", "Campus Online", "Casais", "Clínica Da Alma",
  "Baby", "Coral", "Eventos", "Store",
];

const validateName = (name: string) => /^[A-Za-zÀ-ÿ\s]{3,}$/.test(name);

export function Etapa2({ form, handleChange, onBack, onSubmit }: Etapa2Props) {
  const [loading, setLoading] = useState(false);

  // ---> LÓGICA DE VALIDAÇÃO CONDICIONAL <---
  const isValid = (() => {
    if (form.tipoVoluntario === 'existente') {
      return validateName(form.nome) && form.ministerio.length > 0;
    }
    if (form.tipoVoluntario === 'interessado') {
      // Validação completa para novos interessados
      const isMembroDesdeValid = !!form.membroDesde && form.membroDesde.length === 7;
      const isBatizadoValid = !form.batizado || (form.batizado && !!form.batizadoDesde && form.batizadoDesde.length === 7);
      const isVoluntarioFieldsValid = !!form.voluntarioDesde && form.voluntarioDesde.length === 7 && form.ministerio.length > 0 && !!form.camiseta;
      return isMembroDesdeValid && isBatizadoValid && isVoluntarioFieldsValid;
    }
    return false;
  })();

  const toggleMinisterio = (value: string) => {
    const updated = form.ministerio.includes(value)
      ? form.ministerio.filter((m) => m !== value)
      : [...form.ministerio, value];
    handleChange({ target: { name: "ministerio", value: updated } } as any);
  };

  // Suas outras funções (handleRadioChange, renderSelectYM) continuam aqui...
  const handleRadioChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    handleChange({
      target: { name: "batizado", value: e.target.value === "true" },
    } as any);
  };
  const buildYM = (ano: string, mes: string) => (ano && mes ? `${ano}-${mes}` : "");
  const handleMonthYear = (campo: keyof Pick<FormData, "membroDesde" | "voluntarioDesde" | "batizadoDesde">, tipo: "mes" | "ano", valor: string) => {
    const [anoAtual, mesAtual] = (form[campo] || "").split("-");
    const novoAno = tipo === "ano" ? valor : (anoAtual || "");
    const novoMes = tipo === "mes" ? valor : (mesAtual || "");
    handleChange({ target: { name: campo, value: buildYM(novoAno, novoMes) } } as any);
  };
  const renderSelectYM = (campo: keyof Pick<FormData, "membroDesde" | "voluntarioDesde" | "batizadoDesde">, required = false) => {
    const [ano, mes] = (form[campo] || " ").split("-");
    return (
      <div className="grid grid-cols-2 gap-4 mb-8">
        <div>
          <span className="text-sm text-gray-500 mb-1">Mês</span>
          <select value={mes || ""} onChange={(e) => handleMonthYear(campo, "mes", e.target.value)} required={required} className="w-full text-lg border-b bg-transparent py-2">
            <option value="">–</option>
            {MESES.map((m) => (<option key={m.value} value={m.value}>{m.label}</option>))}
          </select>
        </div>
        <div>
          <span className="text-sm text-gray-500 mb-1">Ano</span>
          <select value={ano || ""} onChange={(e) => handleMonthYear(campo, "ano", e.target.value)} required={required} className="w-full text-lg border-b bg-transparent py-2">
            <option value="">–</option>
            {ANOS.map((a) => (<option key={a} value={a}>{a}</option>))}
          </select>
        </div>
      </div>
    );
  };

  const ministeriosCheckboxGrid = (
    <div className="grid grid-cols-2 sm:grid-cols-3 gap-3 mb-8">
        {ministeriosDisponiveis.map((min) => (
          <label key={min} className="flex items-center gap-2 text-gray-700 cursor-pointer text-base">
            <input
              type="checkbox"
              className="h-4 w-4 rounded border-gray-300 text-purple-600 focus:ring-purple-500"
              checked={form.ministerio.includes(min)}
              onChange={() => toggleMinisterio(min)}
            />
            {min}
          </label>
        ))}
    </div>
  );

  return (
    <motion.div
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      {/* ---> RENDERIZAÇÃO CONDICIONAL DO CONTEÚDO <--- */}
      
      {form.tipoVoluntario === 'existente' ? (
        <>
          {/* === Bloco para quem JÁ É VOLUNTÁRIO (Simplificado) === */}
          <p className="text-2sm text-gray-400 mb-2">2 →</p>
          <h2 className="text-xl font-regular mb-6 text-gray-800">
            Que legal ter você aqui! Por favor, confirme seu nome e em qual ministério você serve.
          </h2>

          <label className="block text-xl text-gray-800 font-light mt-4">Nome Completo *</label>
          <input
            name="nome"
            placeholder="Seu nome completo"
            value={form.nome}
            onChange={handleChange}
            className={`mb-10 font-light text-2xl w-full border-b bg-transparent text-gray-700 placeholder-gray-300 focus:outline-none py-2 ${validateName(form.nome) || form.nome === '' ? 'border-gray-300 focus:border-purple-700 focus:border-b-2' : 'border-red-500'}`}
            required
          />

          <label className="block text-xl font-light text-gray-700 mt-4">Ministérios que serve *</label>
          {ministeriosCheckboxGrid}
        </>
      ) : (
        <>
          {/* === Bloco para quem TEM INTERESSE (Fluxo Completo) === */}
          <p className="text-2sm text-gray-400 mb-2">2 →</p>
          <h2 className="text-xl font-regular mb-6 text-gray-800">
            Agora queremos saber um pouco da sua caminhada com Deus.
          </h2>

          <label className="block text-xl font-light text-gray-700">Membro desde *</label>
          {renderSelectYM("membroDesde", true)}
          
          <label className="block text-xl font-light text-gray-700 mb-2">É batizado? *</label>
          <div className="flex gap-6 mb-6 text-gray-700 text-xl">{/* ...radios Sim/Não... */}</div>
          {form.batizado && (<>{/* ...renderSelectYM("batizadoDesde", true)... */}</>)}
          
          <label className="block text-xl font-light text-gray-700">Voluntário desde *</label>
          {renderSelectYM("voluntarioDesde", true)}

          <label className="block text-xl font-light text-gray-700">Tamanho da camiseta *</label>
          <select name="camiseta" value={form.camiseta} onChange={handleChange} required className="mb-8 w-full text-lg border-b bg-transparent py-2">{/* ...opções... */}</select>

          <label className="block text-xl font-light text-gray-700">Ministérios de interesse *</label>
          {ministeriosCheckboxGrid}
        </>
      )}

      {/* BOTÕES DE NAVEGAÇÃO (Comuns para ambos os casos) */}
      <div className="flex justify-between mt-8">
        <button onClick={onBack} className="bg-gray-800 hover:bg-gray-600 text-white font-bold py-2 px-4 rounded-md">
          Voltar
        </button>
        <button
          onClick={async () => {
            if (loading || !isValid) return;
            setLoading(true);
            await onSubmit();
            setLoading(false);
          }}
          disabled={!isValid || loading}
          className={`font-semibold rounded-md py-3 px-5 transition ${!isValid || loading ? "bg-gray-300 text-gray-500 cursor-not-allowed" : "bg-gray-800 hover:bg-blue-700 text-white"}`}
        >
          {loading ? "Enviando..." : "Enviar Inscrição"}
        </button>
      </div>
    </motion.div>
  );
}