// components/Etapa1.tsx
"use client";

import { motion } from "framer-motion";
import { useState, useEffect } from "react";
// CORREÇÃO APLICADA AQUI
import estadosCidades from "@/lib/estados-cidades.json" assert { type: "json" };

// Interface atualizada para refletir os campos desta etapa
interface FormData {
  nome: string;
  dataNascimento: string;
  sexo: string;
  whatsapp: string;
  email: string;
  estado: string; // Sigla do estado, ex: "SP"
  cidade: string;
}

interface Etapa1Props {
  form: FormData;
  handleChange: (e: React.ChangeEvent<any>) => void;
  onNext: () => void;
  onBack: () => void;
}

// Definindo o tipo dos nossos dados importados
interface Estado {
  sigla: string;
  nome: string;
  cidades: string[];
}

export function Etapa1({ form, handleChange, onNext, onBack }: Etapa1Props) {
  const [cidades, setCidades] = useState<string[]>([]);
  
  const validateName = (name: string) => /^[A-Za-zÀ-ÿ\s]{3,}$/.test(name);
  const validatePhone = (phone: string) => /^\(\d{2}\)\s?9\d{4}-\d{4}$/.test(phone);
  const validateEmail = (email: string) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  const isDateValid = (date: string) => date !== "";

  const isValid =
    validateName(form.nome) &&
    isDateValid(form.dataNascimento) &&
    form.sexo !== "" &&
    validatePhone(form.whatsapp) &&
    form.estado !== "" &&
    form.cidade !== "" &&
    (form.email === "" || validateEmail(form.email));

  useEffect(() => {
    if (form.estado) {
      const estadoSelecionado = estadosCidades.find(
        (e: Estado) => e.sigla === form.estado
      );
      if (estadoSelecionado) {
        setCidades(estadoSelecionado.cidades);
      }
    } else {
      setCidades([]);
    }
  }, [form.estado]);

  const handleEstadoChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const siglaEstado = e.target.value;
    handleChange({ target: { name: "estado", value: siglaEstado } } as any);
    handleChange({ target: { name: "cidade", value: "" } } as any);
  };
  
  const formatPhone = (value: string) => {
    const digits = value.replace(/\D/g, "").substring(0, 11);
    const match = digits.match(/^(\d{2})(\d{5})(\d{4})$/);
    if (match) {
      return `(${match[1]}) ${match[2]}-${match[3]}`;
    }
    return value;
  };

  const selectStyle = (isValid: boolean) =>
    `mb-10 w-full text-2xl font-light border-b bg-transparent placeholder-gray-300 focus:outline-none focus:border-b-2 py-2 cursor-pointer ${isValid ? 'text-gray-700 border-gray-300 focus:border-purple-700' : 'text-gray-400 border-red-500'}`;

  return (
    <motion.div
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <p className="text-2sm text-gray-400 mb-2">1 → </p>
      <h2 className="text-xl font-regular mb-6 text-gray-800">Primeiro de tudo, precisamos das suas informações de contato.</h2>
      
      {/* O resto dos seus campos continua aqui (Nome, etc.) */}
      
      {/* ESTADO */}
      <label className="block text-xl font-light text-gray-700">Estado *</label>
      <select
        name="estado"
        value={form.estado}
        onChange={handleEstadoChange}
        className={selectStyle(form.estado !== '')}
        required
      >
        <option value="">Selecione um estado</option>
        {estadosCidades.map((estado: Estado) => ( // Removido o type assertion desnecessário
          <option key={estado.sigla} value={estado.sigla}>
            {estado.nome}
          </option>
        ))}
      </select>

      {/* CIDADE */}
      <label className="block text-xl font-light text-gray-700">Cidade *</label>
      <select
        name="cidade"
        value={form.cidade}
        onChange={handleChange}
        className={selectStyle(form.cidade !== '')}
        disabled={!form.estado}
        required
      >
        <option value="">
          {form.estado ? "Selecione uma cidade" : "Escolha um estado primeiro"}
        </option>
        {cidades.map((cidade) => (
          <option key={cidade} value={cidade}>
            {cidade}
          </option>
        ))}
      </select>

      {/* BOTÕES DE NAVEGAÇÃO */}
      <div className="flex justify-between mt-8">
        <button
          onClick={onBack}
          className="bg-gray-800 hover:bg-gray-600 text-white font-bold py-2 px-4 rounded-md"
        >
          Voltar
        </button>
        <button
          onClick={onNext}
          disabled={!isValid}
          className={`flex items-center gap-2 font-bold rounded-md px-6 py-2 transition focus:outline-none cursor-pointer ${isValid ? 'bg-gray-800 hover:bg-gray-600 text-white' : 'bg-gray-300 text-gray-500 cursor-not-allowed'}`}
        >
          Próximo <span className="text-sm font-light"> ↵</span>
        </button>
      </div>
    </motion.div>
  );
}