// components/Etapa0.tsx
"use client";

import { motion } from "framer-motion";

interface FormData {
  // A etapa agora depende desses dois campos
  voluntario: boolean | undefined;
  tipoVoluntario: 'existente' | 'interessado' | null;
}

interface Etapa0Props {
  form: FormData;
  handleChange: (e: React.ChangeEvent<any>) => void;
  onNext: () => void;
}

export function Etapa0({ form, handleChange, onNext }: Etapa0Props) {
  const isValid = form.tipoVoluntario !== null;

  return (
    <motion.div className=""
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-xl font-regular mb-8 text-gray-800">
        Você já faz parte do time de voluntários da Comunidade da Fé?
      </h2>

      <div className="flex flex-col space-y-4 mb-10">
        {/* Opção 1: "Sim, já sou voluntário" */}
        <label
          className={`p-4 w-full text-lg border rounded-md cursor-pointer transition text-center ${
            form.tipoVoluntario === 'existente' // <-- Checa o estado específico
              ? "bg-purple-700 border-purple-700 text-white" // Estilo quando selecionado
              : "border-gray-300 text-gray-700 hover:border-purple-500" // Estilo quando não selecionado
          }`}
        >
          <input
            type="radio"
            name="tipoVoluntario" // <-- O nome agora é 'tipoVoluntario'
            value="existente"     // <-- O valor é 'existente'
            className="sr-only"
            checked={form.tipoVoluntario === 'existente'} // <-- Controla o 'checked'
            onChange={handleChange} // <-- Dispara a função do pai
          />
          Sim, já sou voluntário
        </label>

        {/* Opção 2: "Não sou, mas tenho interesse" */}
        <label
          className={`p-4 w-full text-lg border rounded-md cursor-pointer transition text-center ${
            form.tipoVoluntario === 'interessado' // <-- Checa o outro estado específico
              ? "bg-purple-700 border-purple-700 text-white" // Estilo quando selecionado
              : "border-gray-300 text-gray-700 hover:border-purple-500" // Estilo quando não selecionado
          }`}
        >
          <input
            type="radio"
            name="tipoVoluntario" // <-- O nome agora é 'tipoVoluntario'
            value="interessado"    // <-- O valor é 'interessado'
            className="sr-only"
            checked={form.tipoVoluntario === 'interessado'} // <-- Controla o 'checked'
            onChange={handleChange} // <-- Dispara a função do pai
          />
          Não sou, mas tenho interesse
        </label>
      </div>
      
      <button
        onClick={onNext}
        disabled={!isValid}
        className={`flex items-center gap-2 font-bold rounded-md px-6 py-2 transition focus:outline-none cursor-pointer ${
          isValid
            ? "bg-gray-800 hover:bg-gray-600 text-white"
            : "bg-gray-300 text-gray-500 cursor-not-allowed"
        }`}
      >
        Próximo <span className="text-sm font-light"> ↵</span>
      </button>
    </motion.div>
  );
}